    -moz-background-clip: padding;
    background-clip: padding-box;
    overflow: hidden;
  }
  
  .gu-mirror a {
    width: 25%;
  }
  
  .gu-mirror .fileimg {
    height: 75px;
    width: 75px;
    margin: 0 10px 0 0;
    background-color: #ccc;
    background-size: 60px 60px;
    background-repeat: no-repeat;
    background-position: center center;
    display: inline-block;
  }
  
  .gu-mirror .fileimg img{
    height: inherit;
  }
  
  .gu-mirror span {
    margin: 5px 0 0;
    width: 70%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    line-height: 17px;
    color: #748590;
    font-size: 14px;
    float: left;
  }
  
  .gu-mirror .file-text {
    color: #aabac4;
    margin: 0;
    font-size: 12px;
  }
  
  .gu-mirror .file-action {
    position: absolute;
    right: 15px;
    top: 50%;
    transform: translateY(-50%);
  }
  
  .gu-mirror .file-action span {
    width: 15px;
    display: inline-block;
    margin: 0 10px 0 0;
    cursor: pointer;
  }
  
  .gu-hide {
    display: none!important
  }
  
  .gu-unselectable {
    -webkit-user-select: none!important;
    -moz-user-select: none!important;
    -ms-user-select: none!important;
    user-select: none!important
  }
  
  .gu-transit {
    border: 2px dashed #063a52;
    background: #F0F9FF;
    opacity: 0.2;
    margin-left: 5px;
    /* -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=20)";
    filter: alpha(opacity=20) */
  }
  
  .gu-transit .file {
    opacity: 0;
  }
  
  .close-icon {
    width: 16px;
    height: 16px;
    opacity: 0.3;
  }
  
  .close-icon:hover {
    opacity: 1;
  }
  
  .close-icon:before, .close-icon:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 16px;
    width: 2px;
    background-color: #333;
  }
  
  .close-icon:before {
    transform: rotate(45deg);
  }
  
  .close-icon:after {
    transform: rotate(-45deg);
  }